angular
    .module('app')
    .component('fountainTechs', {
        templateUrl: 'app/techs/techs.html',
        controller: TechsController,
        bindings: {
            topic: '<',
            keywords: '<'
        }
    });

/** @ngInject */
function TechsController($http, $timeout) {
    var vm = this;

    var doc_ids = [];
    vm.news = {response: {docs: []}};
    function perdiocReload() {
        $http
            .get('https://www.newscombinator.com/api/search?q=(_val_%3A%22recip(ms(NOW%2FHOUR%2Ccreated_at)%2C3.16e-11%2C2700%2C1)%22^15%20)%20%2B('+encodeURI(vm.keywords)+')%20%2Blanguage%3Aen%20&rows=10')
            .then(function (response) {
                for(var i = 0; i < response.data.response.docs.length; i++) {
                    if(doc_ids.indexOf(response.data.response.docs[i].id) === -1) {
                        doc_ids.push(response.data.response.docs[i].id);

                        vm.news.response.docs.unshift(response.data.response.docs[i]);
                        var items = [];
                        items.push(response.data.response.docs[i]);
                        if (vm.adapterContainer.adapter.isBOF()) {
                            vm.adapterContainer.adapter.prepend(items);
                        }
                        if (Notification.permission === "granted") {
                            var notification = new Notification(items.length+" items in "+vm.topic);
                        }
                    }
                }

                $timeout(perdiocReload, 20000);
            });
    }
    $http
        .get('https://www.newscombinator.com/api/search?q=(_val_%3A%22recip(ms(NOW%2FHOUR%2Ccreated_at)%2C3.16e-11%2C2700%2C1)%22^15%20)%20%2B('+encodeURI(vm.keywords)+')%20%2Blanguage%3Aen%20&rows=25')
        .then(function (response) {
            vm.news = response.data;

            for(var i = 0; i < response.data.response.docs.length; i++) {
                doc_ids.push(response.data.response.docs[i].id);
            }
            vm.adapterContainer.adapter.reload();

            $timeout(perdiocReload, 2500);
        });


    vm.newselements = {};
    vm.newselements.get = function (index, count, success) {
        var i, items = [], item;

        for (i = index; i < index + count; i++) {

            if(i < 0 || i >= vm.news.response.docs.length) {
                continue;
            }
            item = vm.news.response.docs[i];
            items.push(item);
        }


        success(items);
    };



    vm.adapterContainer = { adapter: {
        remain: true
    }};
}

